/* Global */
body {
  background: #2A2A2E;
  font-family: "Roboto";
}

button {
  background: transparent;
  border: none;
}

a, button {
  transition: all 0.15s linear;
}

input, textarea {
  border: 0;
}

img {
  max-width: 100%;
}

::placeholder {
  color: #777777;
}

a:hover {
  text-decoration: none;
}

p, label, h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
}

*:after, *:before {
  box-sizing: border-box;
}

input:focus, textarea:focus {
  outline: none;
}

dl, ol, ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.content {
  /*overflow: hidden;*/
  position: relative;
  z-index: 1;
}

/* Header */
.ngl-header {
  font-family: 'PT Mono', monospace;
  font-size: 12px;
  padding: 14px 0;
}

.navbar-brand {
  margin: 0;
  padding: 0;
}

.nav-link {
  margin: 0;
  padding: 0;
  font-size: 12px;
  line-height: 13px;
  color: white;
  position: relative;
  white-space: nowrap;
}

.nav-link.active {
  color: #7BF7FF;
}

.nav-link:after {
  content: '';
  display: block;
  width: 0;
  height: 1px;
  background: #7BF7FF;
  position: absolute;
  bottom: -8px;
  left: -9px;
  transition: all 0.2s linear;
}

.nav-link.active:after {
  width: calc(100% + 18px);
}

.nav-link:hover {
  color: #7BF7FF;
}

.nav-link:hover:after {
  width: calc(100% + 18px);
}

.navbar-brand {
  margin-right: 79px;
}

.navbar-expand-lg .navbar-nav .nav-link {
  padding: 0;
}

.nav-item {
  margin-right: 25px;
}

.nav-item:last-child {
  margin-right: 0;
}

.ngl-search {
  position: relative;
  margin-right: 39px;
}

.ngl-search__field {
  width: 380px;
  height: 54px;
  background: #DADDFF;
  padding: 0 77px 0 24px;
}

.ngl-search__wrap {
  position: absolute;
  right: -10px;
  top: 40px;
  display: none;
}

.navbar {
  padding: 0;
}

.ngl-search__wrap:after {
  display: block;
  content: "";
  width: 53px;
  height: 63px;
  background-image: url("../img/search-btn.png");
  position: absolute;
  top: -9px;
  right: 0;
}

.neon-btn {
  text-transform: uppercase;
  color: #7BF7FF;
  height: 48px;
  width: 138px;
  border: 1px solid #7BF7FF;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.neon-btn.active {
  filter: drop-shadow(0px 0px 10px #7BF7FF);
}

.neon-btn:hover {
  color: #7BF7FF;
  filter: drop-shadow(0px 0px 10px #7BF7FF);
}

.ngl-social__link {
  margin-right: 5px;
