}

.ngl-header .ngl-social {
  margin-left: 39px;
  margin-right: 31px;
}

.languages {
  height: 21px;
  border-right: 1px solid white;
  padding-right: 7px;
}

.languages a {
  color: white;
  text-transform: uppercase;
  display: none;
}

.languages a.current {
