/* Ngl-request */
.request {
  background: rgba(66, 75, 108, 0.62);
  padding: 78px 95px;
  position: relative;
  width: 1110px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: none;
}

.request__btn {
  margin-left: auto;
}

.request__footer label {
  display: flex;
  align-items: center;
  color: #777;
}

.close-this {
  position: absolute;
  top: 26px;
  right: 35px;
  cursor: pointer;
  transition: all 0.2s linear;
}

.close-this:hover {
  transform: scale(1.2);
}

.field {
  height: 57px;
  width: 255px;
  background: rgba(255, 255, 255, 0.9);
  padding: 0 25px;
  font-size: 16px;
  line-height: 19px;
}

textarea {
  font-size: 16px;
  line-height: 19px;
  background: rgba(255, 255, 255, 0.9);
  padding: 23px 25px;
  resize: none;
  width: 100%;
  height: 172px;
}

.request__row {
  margin-bottom: 26px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.request__row .field:nth-child(3) {
