}

.choice {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.choice__square {
  width: 20px;
  height: 20px;
  background: #EAEAED;
  margin-right: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.choice__square svg {
  opacity: 0;
  transition: all 0.15s linear;
}

input[type="checkbox"] {
  display: none;
}

input[type="checkbox"]:checked + label {
  color: white;
}

input[type="checkbox"]:checked + label svg {
  opacity: 1;
}

label {
  cursor: pointer;
  transition: all 0.15s linear;
}

.ngl-request label {
  color: #777777;
  display: flex;
  align-items: center;
}

.request__footer {
